@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@400;600;700&display=swap");

body {
	font-family: "Montserrat", sans-serif;
	background: var(--gradient-bg);
	color: var(--text-color);
	scroll-behavior: smooth;
	margin: 0;
	padding: 0;
}

.pc-avatar-content .avatar {
	width: 100%;
	position: absolute;
	left: 50%;
	transform: translateX(-50%) scale(1);
	bottom: 2px;
	opacity: calc(1.75 - var(--pointer-from-center));
}

.pc-details {
	width: 100%;
	position: absolute;
	top: 3em;
	display: flex;
	flex-direction: column;
}

.pc-details h3 {
	font-weight: 600;
	margin: 0;
	font-size: min(5svh, 3em);
	margin: 0;
	background-image: linear-gradient(
		to bottom,
		var(--accent-color),
		var(--accent-color)
	);
	background-size: 1em 1.5em;
	-webkit-text-fill-color: transparent;
	background-clip: text;
	-webkit-background-clip: text;
}

.pc-details p {
	font-weight: 600;
	position: relative;
	top: -12px;
	white-space: nowrap;
	font-size: 16px;
	margin: 0 auto;
	width: min-content;
	background-image: linear-gradient(to bottom, #fff, var(--accent-color));
	background-size: 1em 1.5em;
	-webkit-text-fill-color: transparent;
	background-clip: text;
	-webkit-background-clip: text;
}

/* -----------------------------------------
   CARD WRAPPER & BACKGROUND GLOW
----------------------------------------- */
.pc-card-wrapper {
	perspective: 800px;
	position: relative;
	touch-action: none;
}

.pc-card-wrapper::before {
	content: "";
	position: absolute;
	inset: -10px;
	background: var(--glass-bg);
	border: 1px solid var(--glass-border);
	border-radius: var(--card-radius);
	filter: blur(30px) brightness(1.2);
	transform: scale(0.9);
	transition: all 0.5s ease;
}

.pc-card-wrapper:hover::before,
.pc-card-wrapper.active::before {
	filter: blur(40px) brightness(1.5);
	transform: scale(0.9);
}

/* -----------------------------------------
   CARD BODY
----------------------------------------- */
.pc-card {
	height: 80svh;
	max-height: 540px;
	display: grid;
	aspect-ratio: 0.718;
	border-radius: var(--card-radius);
	position: relative;
	background: var(--gradient-bg);
	background-blend-mode: overlay;
	box-shadow: 0 10px 40px rgba(0, 0, 0, 0.5);
	transition: transform 1s ease;
	overflow: hidden;
}

.pc-card:hover,
.pc-card.active {
	transform: rotateX(var(--rotate-y)) rotateY(var(--rotate-x));
	transition: transform 0.3s ease;
}

/* -----------------------------------------
   INSIDE GLASS LAYER
----------------------------------------- */
.pc-inside {
	inset: 1px;
	position: absolute;
	background: var(--glass-bg);
	border-radius: var(--card-radius);
	backdrop-filter: blur(20px);
}

/* -----------------------------------------
   GLARE EFFECT
----------------------------------------- */
.pc-glare {
	position: absolute;
	inset: 0;
	border-radius: var(--card-radius);
	background: radial-gradient(
		circle at var(--pointer-x) var(--pointer-y),
		rgba(255, 255, 255, 0.1),
		transparent 70%
	);
	mix-blend-mode: overlay;
	pointer-events: none;
}

/* -----------------------------------------
   USER INFO ON CARD
----------------------------------------- */
.pc-user-info {
	position: absolute;
	bottom: 20px;
	left: 20px;
	right: 20px;
	z-index: 2;
	display: flex;
	align-items: center;
	justify-content: space-between;
	background: var(--glass-bg);
	border: 1px solid var(--glass-border);
	backdrop-filter: blur(12px);
	border-radius: 0.75rem;
	padding: 12px 14px;
	pointer-events: auto;
}

.pc-user-details {
	display: flex;
	align-items: center;
	gap: 12px;
}

.pc-mini-avatar {
	width: 48px;
	height: 48px;
	border-radius: 50%;
	overflow: hidden;
	border: 1px solid var(--glass-border);
}

.pc-mini-avatar img {
	width: 100%;
	height: 100%;
	object-fit: cover;
	border-radius: 50%;
}

.pc-handle,
.pc-status {
	color: var(--text-color);
	line-height: 1;
}

.pc-handle {
	font-size: 14px;
	font-weight: 600;
}

.pc-status {
	font-size: 12px;
	opacity: 0.7;
}

/* -----------------------------------------
   CONTACT BUTTON
----------------------------------------- */
.pc-contact-btn {
	background: var(--accent-color);
	color: #1f1e1e;
	padding: 8px 16px;
	font-size: 14px;
	font-weight: 600;
	border: none;
	border-radius: 0.5rem;
	cursor: pointer;
	transition: all 0.2s ease;
}

.pc-contact-btn:hover {
	background: #a88b6b;
	transform: translateY(-1px);
}

/* -----------------------------------------
   RESPONSIVE TWEAKS
----------------------------------------- */
@media (max-width: 768px) {
	.pc-card {
		height: 70svh;
		max-height: 450px;
	}
	.pc-handle {
		font-size: 13px;
	}
	.pc-status {
		font-size: 10px;
	}
	.pc-contact-btn {
		padding: 6px 12px;
		font-size: 12px;
	}
}

@media (max-width: 480px) {
	.pc-card {
		height: 60svh;
		max-height: 380px;
	}
	.pc-handle {
		font-size: 12px;
	}
	.pc-status {
		font-size: 9px;
	}
	.pc-contact-btn {
		padding: 5px 10px;
		font-size: 10px;
	}
}

@media (max-width: 320px) {
	.pc-card {
		height: 55svh;
		max-height: 320px;
	}
	.pc-handle {
		font-size: 11px;
	}
	.pc-status {
		font-size: 8px;
	}
	.pc-contact-btn {
		padding: 4px 8px;
		font-size: 9px;
	}
}
