.skills-section {
	padding: 5rem 0;
	background-color: #121212;
}

.skills-section .container {
	max-width: 1250px;
	margin-left: auto;
	margin-right: auto;
	padding-left: 2rem;
	padding-right: 2rem;
	width: 100%;
	box-sizing: border-box;
}

.skills-section .container p {
	text-align: center;
	margin-bottom: 2rem;
	color: #ccc;
}

.skill-card {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	padding: 1.5rem;
	border-radius: var(--card-radius);
	box-shadow: 0 6px 15px rgba(0, 0, 0, 0.3);
	background-color: #1e1e1e;
	height: 180px;
	user-select: none;
	transition: transform 0.3s ease;
	cursor: default;
}

.skill-label {
	color: #eee;
	font-weight: 600;
	font-size: 1.25rem;
}
